import React, { useEffect, useState } from "react";

import { LastRow } from "./styles";
import DayVisitsReport from "./AnalyticsReports/dayVisitsReport";
import CountriesReport from "./AnalyticsReports/countriesReport";
import PageviewsReport from "./AnalyticsReports/pageviewReport";
import SourceReport from "./AnalyticsReports/sourceReport";
import BrowsersReport from "./AnalyticsReports/browsersReport";
import DevicesReport from "./AnalyticsReports/devicesReport";
import DayVisitsReport_realtime from "./AnalyticsReports/dayVisitsReport_realtime";
import OtherReports from "./AnalyticsReports/otherReports";
import UserdataReports from "./AnalyticsReports/userdataReports";
import DevicedataReports from "./AnalyticsReports/devicedataReports";
import WebPerformanceReport from "./AnalyticsReports/webPerformanceReport";
import TrafficAnalysisReport from "./AnalyticsReports/trafficAnalysisReport";
import './style.css'

import { GoogleLogin } from 'react-google-login';
import * as google from '../../../../assets/Clean Walk/google.svg'
import { Sociallogin } from '../../../../utils/actions'
import { useActiveScreen } from "../../../../store/ActiveScreen";
import { useAuth } from "../../../../store/authStore";
import { userstore } from "../../../../store/userStore";
import { realtime_queryReport } from "./AnalyticsReportingApi/realtime_queryReport";
import { addDays } from "date-fns";
import { useColourObjectStore } from '../../../../store/ColourStore';


const AnalyticsDashboard = () => {

  const { colourObj } = useColourObjectStore();

  const { setActiveScreen } = useActiveScreen();
  const { setAuth } = useAuth()
  const { setselfuser } = userstore()
  const [activetab, setactivetab] = useState(true)

  const [active, setActive] = useState(0);
  const [startDate, setStartDate] = useState(addDays(new Date(), -10));
  const [endDate, setEndDate] = useState(new Date());

  const [responce, setresponce] = useState('')

  useEffect(() => {

    const request = {
      startDate,
      endDate,
      metrics: [
        {
          "name": "activeUsers"
        },
        {
          "name": "conversions"
        },
        {
          "name": "eventCount"
        },
        {
          "name": "screenPageViews"
        },
      ],
      dimensions: [
        {
          "name": "city"
        },
        {
          "name": "cityId"
        },
        {
          "name": "country"
        },
        {
          "name": "countryId"
        },

      ],
    };
    realtime_queryReport(request)
      .then((resp: any) => {
        console.log('????', resp)
        setresponce(resp.status)
      })
      .catch((error: any) => {
        console.error('????', error)
        setresponce(error.status)
      });
  }, [startDate, endDate]);




  const handleClick = (e: any) => {
    const index = parseInt(e.target.id);
    if (index !== active) {
      setActive(index);
    }
  };

  return (
    <>
      {(String(responce) !== '401') ?

        <div className="results_container" style={{ borderColor: colourObj.color_m1 }}>
          {/* <div className="Tab_day_night_container">

            <div className="tab_day_night">
              <div onClick={() => {
                setactivetab(true)
              }}
                className={activetab ? "tab_active" : "tab_inactive"}
                style={activetab ? { textDecorationColor: colourObj.color_m1 } : {}}>
                REAL-TIME REPORT
              </div>
              <div onClick={() => {
                setactivetab(false)
              }}
                className={activetab ? "tab_inactive" : "tab_active"}
                style={activetab ? {} : { textDecorationColor: colourObj.color_m1 }}>
                GENERATED REPORT
              </div>
            </div>

          </div> */}

          {/* {activetab ?

            <div className="result_tab">
              <DayVisitsReport_realtime title={"Users"} />
            </div>

            : */}

          <div className="result_tab">

            <div className="tab_day_night2">

              <div onClick={(e) => { handleClick(e) }} className={active === 0 ? "tab_inactive2" : "tab_active2"}
                style={active === 0 ? {} : { textDecorationColor: colourObj.color_m1 }}
                active={active === 0} id={0}
              >
                User Data</div>

              <div onClick={(e) => { handleClick(e) }} className={active === 1 ? "tab_inactive2" : "tab_active2"}
                style={active === 1 ? {} : { textDecorationColor: colourObj.color_m1 }}
                active={active === 1} id={1}
              >
                Device Data</div>

              <div onClick={(e) => { handleClick(e) }} className={active === 2 ? "tab_inactive2" : "tab_active2"}
                style={active === 2 ? {} : { textDecorationColor: colourObj.color_m1 }}
                active={active === 2} id={2}
              >
                Website/Webapp/App Performance</div>

              <div onClick={(e) => { handleClick(e) }} className={active === 3 ? "tab_inactive2" : "tab_active2"}
                style={active === 3 ? {} : { textDecorationColor: colourObj.color_m1 }}
                active={active === 3} id={3}
              >
                Traffic Analysis</div>

            </div>



            {(active === 0) &&
              <UserdataReports viewID={"248657837"} />
            }

            {(active === 1) &&
              <DevicedataReports viewID={"248657837"} />
            }

            {(active === 2) &&
              <WebPerformanceReport viewID={"248657837"} />
            }

            {(active === 3) &&
              <TrafficAnalysisReport viewID={"248657837"} />
            }

          </div>

          {/* } */}
        </div>

        :

        <>

          <div className="login_button_container">

            <GoogleLogin
              clientId="581422038025-rte3a06d7kumasu887n64uikerfigmiv.apps.googleusercontent.com"
              render={renderProps => (
                <button className="googlebutton shadow" onClick={renderProps.onClick} disabled={renderProps.disabled}>
                  <img src={google} className="login_googleicon" />
                  <div className="buttontext">Continue with Google</div>
                </button>
              )}
              buttonText="Login"
              onSuccess={Response => {
                console.log(Response)
                localStorage.setItem("Bearer", JSON.stringify(Response.accessToken))
                window.location.reload();
              }}
              onFailure={(Response: any) => { console.log(Response) }}
              cookiePolicy={'single_host_origin'}
            />

          </div>

        </>

      }
      {/* <OtherReports viewID={"241653669"} /> */}

      {/* <DayVisitsReport
        metric={"ga:users"}
        title={"Users"}
        viewID={"248657837"}
      />
      <DayVisitsReport
        metric={"ga:sessions"}
        title={"Sessions"}
        viewID={"248657837"}
      />
      <CountriesReport viewID={"248657837"} />
      <PageviewsReport viewID={"248657837"} />
      <SourceReport viewID={"248657837"} />
      <LastRow>
        <BrowsersReport viewID={"248657837"} />
        <DevicesReport viewID={"248657837"} />
      </LastRow> */}

    </>

  );
};

export default AnalyticsDashboard;